// 获取图片URL的辅助函数
export function getWorkImage(category: string, id: number, size: 'thumbnail' | 'full' = 'full') {
  const categoryData = imageConfig.works[category as keyof typeof imageConfig.works]
  if (!categoryData) return ''
  
  const work = categoryData.find((w: any) => w.id === id)
  if (!work) return ''
  
  return work[size]
}

export function getCampusImage(id: number) {
  const item = imageConfig.campus.find(item => item.id === id)
  return item ? item.src : ''
}